      width: 100%;
  
    }
  }
  .modal__cols {
    display: block;
    margin: 0 0;
  }

  .modal__col {
    width: 100%;
    padding: 0;
  }

  .modal__form .form-group textarea {
    height: 100px;
  }
  .modal-open .modal {
    padding-left: 0 !important;
  }

  .modal-body {
    padding: 0;
  }
  .modal__form .form-group label {
    font-size: 12px;
line-height: 100%;
  }
  .modal__form .form-group  {
    input, select {
      height: 44px;
    }
  }
  .dropdown__label {
    height: 44px;
    font-weight: 500;
font-size: 14px;
padding: 0 18px;
line-height: 40px;
  }
  .filebutton {
    font-size: 14px;
  }
  .fileupload {
    margin: 22px 0;
  }

  .modal-title {
    font-weight: 700;
    font-size: 20px;
    line-height: 100%;
  }

  .modal-subtitle {
    font-size: 15px;
  }

  .check label {
    font-size: 12px;
  }
